import {login} from '../../common/js/common.js'
var db = wx.cloud.database()
var users = db.collection('user')
export default{
	name:'Profile',
	data(){
		return{
			isLogin:false,
			userinfo:[]
		}
	},
	methods:{
		getUserInfo(e){
			console.log(e)
			let info = e.detail.userInfo
			login(info)
			.then(res => {
				this.hasLogin()
			})
			.catch(err => {
				
			})
		},
		hasLogin(){
			users.get()
			.then(res => {
				console.log(res)
				if(res.data.length === 0){
					console.log('没登录')
					this.isLogin=false
				}else{
					this.isLogin=true
					this.userinfo = res.data
				}
			})
			.catch(err => {
				console.log(err)
			})
		}
	},
	onShow() {
		this.hasLogin()
	}
	
}
